@import url("https://fonts.googleapis.com/css2?family=Titan+One&display=swap");
* {
  padding: 0;
  margin: 0;
  font-family: "Titan One", cursive;
}
body {
  /* background-color: plum; */
  /* background-size: cover;

  height: 100vh;
  padding: 0;
  margin: 0; */
  background-image: url(./space.gif);
}
.header {
  width: 100%;
  height: 80px;
  /* background-color: lightblue; */
  dispplay: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  top: 0;
  color: plum;
  z-index: 12;
}

.header .search-box input {
  outline: none;
  border: none;
  width: 350px;
  height: 30 px;
  font-size: 1.2rem;
  padding: 0 1rem;
}

.container {
  width: 100%;
  margin: 100px auto;
  display: flex;
}

.animeInfo {
  width: 320px;
  height: 100%;
  /* background-color: lightblue; */
  position: fixed;
  left: 0;
  top: 0;
}

.anime-row {
  display: flex;
  flex-direction: column;
}

.anime-row .row {
  display: inline-flex;
  gap: 20px;
  margin-left: 20rem;
}

.anime-row .text_heading {
  margin-left: 20rem;
  margin-top: 1rem;
}
.card {
  width: 230px;
  height: 300px;
  padding: 0 0 1rem 0;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  text-align: center;
  overflow: hidden;
  position: relative;
}
.card img {
  width: 100%;
  height: 80%;
}
.anime-content {
  padding: 40px;
  margin: 80px 0;
}
.anime-content img {
  width: 150px;
}
.overlay {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0;
  padding: 1rem;
  background-color: lightblue;
  border-radius: 10rem 3rem 0 0;
  text-align: justify;
  opacity: 0;
  transition: 0.5s;
}
.anime-info:hover .overlay {
  opacity: 1;
  height: 90%;
}
.overlay .synopsis {
  width: 80%;
  height: 50%;
  margin: 0 auto;
  padding: 1rem;
  overflow: auto;
}
.mylist {
  position: absolute;
  bottom: 20px;
  right: 50px;
  padding: 0.5rem;
  background-color: plum;
  font-size: 1rem;
  font-weight: bold;
}
h4 {
  color: aliceblue;
}
h3 {
  color: aliceblue;
}
p {
  color: aliceblue;
}
